import React, { useEffect, useState, useContext } from "react";
import { useParams, useHistory } from 'react-router-dom';

import Button from "../../shared/components/FormElements/Button";
import Card from "../../shared/components/UIElements/Card";
import Input from "../../shared/components/FormElements/Input";
import { VALIDATOR_REQUIRE, VALIDATOR_MINLENGTH } from "../../shared/util/validators";
import { useForm } from '../../shared/hooks/form-hook';
import { useHttpClient } from "../../shared/hooks/http-hook";
import { AuthContext } from "../../shared/context/auth-context";
import './PlaceForm.css';
import ErrorModal from "../../shared/components/UIElements/ErrorModal";
import LoadingSpinner from "../../shared/components/UIElements/LoadingSpinner";

const ModifyPlace = () => {
    const auth = useContext(AuthContext);
    const { isLoading, error, sendRequest, clearError } = useHttpClient();
    const [loadedPlace, setLoadedPlace] = useState();

    const placeId = useParams().placeId;
    const history = useHistory();
  
    const [formState, inputHandler, setFormData] = useForm(
      {
        title: {
          value: '',
          isValid: false
        },
        description: {
          value: '',
          isValid: false
        },
        location: {
            value: '',
            isValid: false
        },
        address: {
            value: '',
            isValid: false
        },
        creator: {
            value: '',
            isValid: false
        }
      },
      false
    );
    useEffect(() => {
      const fetchPlaces = async () => {
        try {
          const responseData = await sendRequest(`${process.env.REACT_APP_BACKEND_URL}/places/${placeId}`);
          setLoadedPlace(responseData.place);
          setFormData(
            {
              title: {
                value: responseData.place.title,
                isValid: true
              },
              description: {
                value: responseData.place.description,
                isValid: true
              },
              location: {
                value: responseData.place.location,
                isValid: true
              },
              address: {
                value: responseData.place.address,
                isValid: true
              },
              creator: {
                value: responseData.place.creator,
                isValid: true
              }
            },
            true
          );
        } catch (err) {}
      }
      fetchPlaces();
    }, [sendRequest, placeId, setFormData]);
  
    const placeUpdateSubmitHandler = async event => {
      event.preventDefault();

      try {
        await sendRequest(`${process.env.REACT_APP_BACKEND_URL}/places/${placeId}`, 'PUT', JSON.stringify({
          title: formState.inputs.title.value,
          description: formState.inputs.description.value,
          location: formState.inputs.location.value,
          address: formState.inputs.address.value,
          creator: formState.inputs.creator.value
        }), {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + auth.token
        });
        history.push('/' + auth.userId + '/places');
      } catch (err) {}
    };

    if (isLoading) {
      return (
        <div className="center">
          <LoadingSpinner />
        </div>
      );
    }
  
    if (!loadedPlace && !error) {
      return (
        <div className="center">
          <Card>
            <h2>Could not find place!</h2>
          </Card>
        </div>
      );
    }
  
    return (
      <React.Fragment>
        <ErrorModal error={error} onClear={clearError} />
        { !isLoading && loadedPlace && (
        <form className="place-form" onSubmit={placeUpdateSubmitHandler}>
          <Input
            id="title"
            element="input"
            type="text"
            label="Title"
            validators={[VALIDATOR_REQUIRE()]}
            errorText="Please enter a valid title."
            onInput={inputHandler}
            initialValue={loadedPlace.title}
            initialValid={true}
          />
          <Input
            id="description"
            element="textarea"
            label="Description"
            validators={[VALIDATOR_MINLENGTH(5)]}
            errorText="Please enter a valid description (min. 5 characters)."
            onInput={inputHandler}
            initialValue={loadedPlace.description}
            initialValid={true}
          />
          <Input
            id="location"
            element="textarea"
            label="Location"
            validators={[VALIDATOR_MINLENGTH(5)]}
            errorText="Please enter a valid location (min. 5 characters)."
            onInput={inputHandler}
            initialValue={loadedPlace.location}
            initialValid={true}
          />
          <Input
            id="address"
            element="textarea"
            label="Address"
            validators={[VALIDATOR_MINLENGTH(5)]}
            errorText="Please enter a valid address (min. 5 characters)."
            onInput={inputHandler}
            initialValue={loadedPlace.address}
            initialValid={true}
          />
          <Input
            id="creator"
            element="textarea"
            label="Creator"
            validators={[VALIDATOR_MINLENGTH(5)]}
            errorText="Please enter a valid creator (min. 5 characters)."
            onInput={inputHandler}
            initialValue={loadedPlace.creator}
            initialValid={true}
          />
          <Button type="submit" disabled={!formState.isValid}>
            UPDATE PLACE
          </Button>
        </form> )}
      </React.Fragment>
    );
  };
  
  export default ModifyPlace;